import React from 'react'
import { isEmpty } from 'ramda'
import { Link } from 'react-router-dom'
import SectionTitle from 'components/SectionTitle'
import Image from 'components/Image'
import VerticalSpacer from 'components/VerticalSpacer'
import Text from 'components/Text'
import Button from 'components/Button'
import Footer from 'components/Footer'
import Resource from 'containers/Resource'
import getRoute from 'utils/getRoute'
import routes from 'routes'
import { Container, Customer } from './styles'

export default function CompanyScreen (props) {
  return (
    <Resource
      resource='AnamneseCompany'
      id={props.match.params.slug}
      autoFetch
      spinner
    >
      {({ detailedRecord }) => {
        if (isEmpty(detailedRecord)) {
          window.location.href = '/'
          return
        }
        return (
          <React.Fragment>
            <Container>
              <Image src={detailedRecord.roteiro.empresa.logo.url} alt='Logo' style={{marginBottom: 50}} />
              <VerticalSpacer space={40}>
                <SectionTitle
                  size='45px'
                  color='text'
                  style={{fontWeight: 'normal'}}
                >
                  Voc se preocupa com a sua sade, no ?<br />
                  A <Customer>{detailedRecord.roteiro.empresa.nome}</Customer> tambm!
                </SectionTitle>
                <Text size='18px' lineHeight={2}>
                  Ns tambm acreditamos que a preveno  sempre o melhor tratamento.
                </Text>
                <Text size='18px' lineHeight={2}>
                  Por isso, estamos lanando um programa que consiste no rastreamento e diagnstico dos principais tipos de cncer. Atravs de um sistema pioneiro e extremamente inteligente, colheremos algumas informaes para estratificar o seu risco para desenvolvimento dos 5 principais tipos de cncer:
                </Text>
                <Text
                  size='18px'
                  lineHeight={2}
                  className='list'
                  component='div'
                >
                  <ul>
                    <li>Cncer de Mama</li>
                    <li>Cncer de Prstata</li>
                    <li>Cncer de Colo de tero</li>
                    <li>Cncer de Clon (intestino)</li>
                    <li>Cncer de Pulmo</li>
                  </ul>
                  Esses 5 tipos de cncer so responsveis por quase 70% de todos os tipos de cncer e tm uma coisa em comum: <strong>PODEM SER DETECTADOS PRECOCEMENTE!</strong>
                </Text>
                <SectionTitle
                  size='45px'
                  color='text'
                  style={{ fontWeight: 'normal' }}
                >
                  Basta preencher a anamnese a seguir.
                </SectionTitle>
                <Text size='18px' lineHeight={2}>
                  Os dados fornecidos por voc so absolutamente sigilosos e sero usados para analisar seu risco de desenvolvimento de algum cncer. Aps anlise da PreviNeo, daremos um retorno aos colaboradores para que as medidas adequadas de preveno sejam tomadas.
                </Text>
                <Text size='18px' lineHeight={2}>
                  Ento, vamos l?
                </Text>
                <Button
                  component={Link}
                  to={getRoute(routes.questionnaire.companyPatient, {
                    companyId: detailedRecord.roteiro.empresa.id,
                    hash: detailedRecord.hash
                  })}
                  arrow
                  block
                >
                  Iniciar preveno
                </Button>
              </VerticalSpacer>
            </Container>
            <Footer />
          </React.Fragment>
        )
      }}
    </Resource>
  )
}



